
.iconSprite(){
	background-image: url(../img/sprite.png);
}
.icon-jiao(@isHover: false){

	.hover (@a) when (@a = false) {
		.iconSprite();
		width: 19px;
		height: 11px;
	}
	.hover(@isHover);
	
	background-position: -83px -64px;
}

[class^="icon-"],
[class*=" icon-"]{
	.iconSprite();
}
.icon-jiao{
	background-position: -83px -64px;
	width: 19px;
	height: 11px;
}


