/* 初始化默认设置 */
html{height:100%;background:#eee;overflow-y:auto;}
body{font:14px/1.5em Microsoft YaHei, ~"\5fae\8f6f\96c5\9ed1,\5b8b\4f53", Arial;width:100%;height:100%;color:#333;background:#eee;}
textarea,input,select,table{font-size:14px;}
select,input{vertical-align:middle}
fieldset,img{border:0 none}
table{margin:0;border-collapse:collapse;border-spacing:0}
button[disabled], html input[disabled] {cursor:default}
a{
	color:#333;
	&:hover{
		color:#f60
	}
}

/* 全局颜色 */
.col-green{color:#02ca3f}
.col-red{color:#ff5a00}
.col-yellow{color:#ff0101}
.col-blue{color:#09c}
.col-gray{color:#666}
.col-gray2{color:#999}

/* 布局  */
.lt-header{
	width:100%;height:60px;background:#0099cc;position:fixed;z-index:99;
	.logo{
		float:left;padding:12px 0 0 10px;
	}
	.login{
		float:right;height:60px;line-height:60px;padding:0 40px 0 0;
		
		a{color:#fff;}
	}
}


.lt-main{padding-top:60px;background:#eee;}

.lt-left{
	width:170px;height:100%;z-index:999;position:fixed;background:#293038;
	
	.fn-clear();

	.nav{padding:40px 0;}
	a{color:#fff;display:inline-block;}
	.nav_dl{
		position:relative;
	
		dt{height:37px;line-height:37px;font-size:14px;color:#fff;}
		dd{display:none;}
		&.cur{
			dt{
				background:#22282e;
				.jiao{border-color: transparent transparent #fff transparent ;border-width:7px 5px;margin:8px 20px 0 0;}
			}
			dd{display:block;}
		}
	}

	.link{
		height:37px;line-height:37px;width:100%;text-align:center;position:relative;

		i{float:left;margin:8px 10px 0 20px;}
		span{float:left;position:absolute;left:57px;}
		.jiao{border-color:#fff transparent transparent transparent ;border-width:7px 5px;margin:15px 20px 0 0;float:right;}
	}

	.subLink{
		height:37px;line-height:37px;width:100%;text-align:center;margin-top:5px;font-size:12px;
		&.cur{background:#0099cc;}
	}
}


.lt-right{
	min-height:450px;min-width:750px;padding:20px 15px 20px 190px;
	//max-width:1190px;
}
.lt-box{padding:20px;background:#fff;border:1px solid #e0e0e0;.fn-clear();}


/* 预定流程 公共部分 */
.lt-head{
	height:65px;background:#0099cc;

	.logo{float:left;margin:15px 0 0 0;}
	.login{float:right;margin-top:20px;line-height:30px;}
	.icon-login-a3{ vertical-align: middle;margin-right:4px;}
	a{
		color:#fff;margin-left:10px;

		&.loginOut{color:#f0d700;}
	}
}

.lt-box1200{width:1200px;margin:0 auto;.fn-clear();}

.lt-content{width:1200px;margin:0 auto 20px;.fn-clear();}