.mod-forms{
	.fn-clear();
	
	background:#fff;
	
	.color999{color:#999;}
	.fn-color{color:#09c}
	
	.ui-form{
		.fn-clear();
		padding:10px 0;

		span.ui-label{
			display: block;
			float: left;
			text-align: right;
			padding: 0 5px 0 0;
			width:auto
		}

		.ui-btn-add{background:#9dd46c;border-color:#9dd46c;}
		//> label .ui-label{color:#999;}
		span.ui-label{}
	}
		.ui-textarea{width:500px;}
		.ui-label{
			width:120px;text-align:right;padding-right:5px;

			&.ui-checkbox-label{padding-right:10px;}
		}
		.ui-text{float:left;padding:0 5px 0 0;line-height:32px;}
		.ui-input{margin:0 5px 0 0}
		.ui-form-message{padding:0 0 0 10px}
	
	
	
	
	.hidden{overflow:hidden;padding-right: 30px;line-height:32px;}
		.table{
			width:100%;
			th{background:#efefef;line-height:50px;text-align:center;color:#888;}
			td{line-height:50px;border-bottom:1px solid #f0f5fb;text-align:center;background:#fff;color:#293038;}
		}

		
}
