.mod-table{
	
	background:#fff;padding:20px;border:1px solid #e0e0e0;
		
	.fn-color{color:#0099cc;}
	.fn-color-666{color:#666;}
	.fn-color-yellow{color:#ff9100;}
	.fn-14{font-size:14px;}
	.ui-input-w70{width:70px;}

	.spanLeft{float:left;text-align:left;padding:0 0 0 20px;}
	
	/* 表格独立头 */
	.header{
		width:100%;
		
		th{line-height:28px;color:#999;text-align:center;padding-bottom: 10px;}
	}
	.itemTable{border:1px solid #e0e0e0;margin-bottom:20px;}
	.itemTable-last{margin:0}
	
	/* 大型表格 */
	.table_a{
		width:100%;
		th{background:#fbfbfb;line-height:50px;text-align:center;color:#888;}
		td{padding:13px 0;line-height:24px;border-bottom:1px solid #f0f5fb;text-align:center;background:#fff;color:#333;}
	}
	.nothing{width:150px;height:130px;margin:40px auto;text-indent:-999em;overflow:hidden;background:url(../img/message-tips.png) no-repeat;}
	
	/* 头部型table 如：/business/goods/ */
	.table_title{
		width:100%;font-size:12px;

		a{font-size:12px;}
		th{height:60px;line-height:60px;text-align:center;background:#f4f4f4;color:#999;}
		td{line-height:30px;padding:0 15px;}
			.padding-up20{
				padding-top:20px;
				.fn-clear();
			}
			.padding-down20{
				padding-bottom:20px;
				.fn-clear();
			}
			
			.emLeft{display:inline-block;width:90px;text-align:right;color:#999;}
			.spanRight{float:right;}
			.fn-20{font-size:20px;color:#0099cc;padding:0 20px 0 10px;}
	}

	.ui-btn{height:30px;line-height:30px;padding:0 24px;}

	
	.ertong{
		display:inline-block;position:relative;

		.icon{border:1px solid #999;display:inline-block;height:0;width:0;border-color:#999 transparent transparent;border-width:5px;margin:0 0 0 5px;vertical-align:middle;}
	}
		.alertBox{
			overflow:hidden;position:absolute;top:23px;left:-30px;z-index:999;min-width:190px;

			div{overflow:hidden;background:#fff;margin-top:10px;padding:5px 10px;border:1px solid #ececec;text-align:left;.border-radius(4px);}
			i{position:absolute;left:30px;z-index:9999;}
		}

}

.mod-table-lg{

	.table_a{
		width:100%;font-size:12px;
		th{background:#f5f5f5;}
	}
}

/* 小型表格 */
.mod-table-small{

	.table_a{
		th{background:none;font-size:14px;}
		td{border-bottom:none;border-top:1px solid #ddd;background:none;font-size:12px}
	}	
}


/* 表格标题 */
.mod-table-title{
	table{width:100%;}
	th{line-height:28px;color:#999;text-align:center;padding-bottom: 10px;}
}
