.page-company{
	
	textarea{width:500px;height:100px;}
	.step{
		overflow:hidden;
		
		
	}

	.box{
		padding:20px;background:#fff;
		.fn-clear();
		
		.mod-forms{padding:20px 0;}
	}

	.boxTable{
		background:#fff;padding-bottom:40px;
		.fn-clear();
		.span{float:left;padding:0 0 0 20px;text-align:left;}
	}

	.priceBnt{
		display:block;height:40px;width:100%;line-height:40px;background:#f5f2f2;text-align:center;color:#666;font-size:18px;

		.icon{
			border-color: #999 transparent transparent;border-style: solid dashed dashed;border-width:10px;height: 0;width: 0;background:none; vertical-align: middle;margin:5px 10px 0 0;
			&.icon-angle-up{border-color:transparent transparent #999;margin:-15px 10px 0 0;}
		}

	}

	/* 门票管理 */
	.titleTable{margin-bottom:20px;}
	.content{
		.fn-clear();
	}
		.itemTable{
			background:#fff;margin-bottom:10px;
			box-shadow:1px 1px 2px #ddd,-1px -1px 2px #ddd;
		}
}