.page-business{

	.fn-color-yellow{color:#ff9600}
	.fn-color-red{color:#ff0101;}
	.fn-color-green{color:#02ca3f;}
	.fn-color-blue{color:#0099cc;}
	.fn-color-666{color:#666;}


	.location{
			color:#008fbf;margin-bottom:10px;
		a{font-size:12px;color:#008fbf;
			&.cur{color:#999;}
		}
	}
	/* 预定流程 */

	.step{
		height:36px;line-height:36px;background:url(../img/step1.png) no-repeat;margin:28px auto;
		.fn-clear();

		&.stepBg2{background:url(../img/step2.png) no-repeat;}
		&.stepBg3{background:url(../img/step3.png) no-repeat;}
		li{
			width:34%;float:left;text-align:center;font-family:"宋体";font-weight:600;color:#666;

			&.last{width:32%;}
			&.cur{color:#fff;}
		}
	}
	.ltContent{padding:20px;background:#fff;border:1px solid #ddd;min-height:800px;}
	.ltLeft{
		width:945px;float:left;
		
			.title{
				height:34px;line-height:33px;border-bottom:1px solid #ddd;margin:20px 0;

				span{padding:0 5px;display:inline-block;height:33px;border-bottom:2px solid #ff9100;font-size:18px;color:#333;}
			}
			
		.btnBox{padding:20px 0;text-align:center;}
		textarea{width:500px;height:110px;}
	}

			/* 点击 加减 */
			.selectNum{
				height:30px;line-height:30px;
				.fn-clear();

				.wrap{
					a{display:inline-block;float:left;text-align:center;width:32px;height:30px;font-size:14px;color:#333;background:#e5e5e5;}
					input{display:inline-block;width:32px;float:left;height:14px;text-align:center;padding:7px 0;border: 1px solid #e5e5e5;margin:0 5px;}
				}
			}
	.ltRight{
		width:240px;float:right;

			.price{
				border:1px solid #0099cc;
				
				.title{height:38px;line-height:38px;padding:0 12px;background:#0099cc;color:#fff;font-size:16px;}
				.content{padding:12px;}
				.item{
					line-height:30px;font-size:12px;
					.fn-clear();

					span{color:#999;}
					.itemNum{float:right;color:#ff9600;}
				}
				.bottom{line-height:40px;border-top:1px dashed #ddd;}
			}
	}
	
	/* 预定流程-成功定制 */
	.success{
		background:#f5f5f5;padding:50px 40px 90px;
		.fn-clear();
			.item{margin-bottom:20px;}
			.ui-btn{margin-right:20px;height:38px;line-height:38px;font-size:16px;width:130px;}
			.fn-color-red{color:#f60909;}
		.left{
			width:370px;height:240px;padding:20px 0 0 0;float:left;border-right:1px solid #ddd;margin-left:150px;position:relative;
			
			.icon-success{position:absolute;left:-65px;}
			.fn-36{font-size:36px;line-height:50px;}
			
			.payWay{
				
				span{padding-right:20px;}
				.fn-color-yellow{font-size:20px;}
				.fn-16{font-size:16px;margin-bottom:5px;}
			}
			.mar30{margin:30px auto;}
			
		}
		
		.right{
			width:324px;margin-left:85px;float:left;padding:30px 0 0 0;

			.item{
				font-size:12px;margin-bottom:4px;

				.fn-color-yellow{font-size:20px;}
			}
				.span{color:#999;padding-left:10px;}
		}

	}


	/* 采购订单 */
	.link_a{color:#0099cc;margin:0 5px;}


	/* 采购订单-待支付 */
	.orderBox{
		background:#fff;border:1px solid #ddd;font-size:12px;

		.ui-form{padding:0;}

		.top{
			font-size:16px;color:#999;padding:20px 20px;line-height:34px;background:#f4f4f4;

			span{font-size:20px;}
			.fn-color-red{font-size:16px;}
		}

		.content{
			padding:20px;



		}
			/* 订单信息 */
			.information{
				padding:10px 0 40px 0;
				.fn-clear();

				.left{
					width:50%;min-height:180px;border-right:1px solid #ddd;float:left;
				}

				.right{
					width:40%;padding:0 5%;float:left;margin-right:-1px;

					.title{margin-bottom:20px;text-align:center;color:#999;}
				}
				.fn-18{font-size:18px;}
			}
			/* 退款 */
			.refundTop{
				color:#999;

				.ui-btn{margin:0 5px 0 10px;font-size:14px;}
			}
			.refundBox{padding:20px 40px;}
	}
	
	/* 订单信息 */
	.apply_refund{
		
		.title{font-size:20px;margin:10px 0 10px;}
		.content{padding:30px 68px 50px;background:#f5f5f5;}
		.item{
			font-size:12px;line-height:30px;

			span{color:#999;}
		}
		.wrap{margin:0;display:inline-block;}
		.mod-table-small{max-width:570px;margin:10px 0 20px;}
	}
}
